function Code1() {

    const codeText =
        `

// React-Window   ( RW)
//          
//        . 


        RW 
    .



1.    .
const today = moment(new Date());
const loadDateData = async (criteria, length, direction) => {
    //   
    // criteria =>  
    // length =>        
    // direction =>  ?  ?  

    const returnData = [] //   
    const startPoint = direction === 2 ? 0 : -length; //   1
    const endPoint = direction === 1 ? 0 : length; //   2

    for (var i = startPoint; i <= endPoint; i++) {
        returnData.push(moment(criteria).add(i, 'days'))
    } //    retrunData  push
    return returnData
}

2. useEffect   Mount       . 
      20,     41 

    useEffect(() => {
        const mountLoad = async () => {
            const mountData = await loadDateData(today, 20, 0)
            setDateData(mountData);
            setDataLoading(true);
        }
        mountLoad()
    }, [])


3.   ,  7  ,  
     Div AutoSizer   9   
      +   +    

<AutoSizer> 
//     
    {({ height, width }) => {
        const division9 = width / 9 
        // 9  
        if (!dataLoading) return; 
        return (
            <div
                className='list-wrap'
                style={{
                    width: width,
                    height: height
                }}
            >
            //  Div
                <div
                    style={{
                        width: division9, 
                        height: height,
                    }}
                    className='list-but'
                    onClick={() => listBut(-7)}
                >
                    <p></p>
                </div>
                // preBut
                <List
                // List = RW   Component
                    className='no-scroll'
                    height={height} 
                    // 
                    itemCount={dateData?.length} 
                    //  
                    itemSize={division9} 
                    //   Div 
                    width={division9 * 7} 
                    // RW   (     x7 )
                    layout='horizontal'
                    //   
                    onItemsRendered={handleTile}
                    //   
                    // 4  
                    ref={listRef}
                    initialScrollOffset={(20 - 3) * division9}
                    //     
                    //  Mount   41
                    //  21     
                    // 20 -3  
                >
                    {({ index, style }) => (
                        //   Component
                        <Tile
                            index={index}
                            style={style}
                            data={dateData}
                            onClick={clickTile}
                            chooseIndex={chooseDate}
                            selectDate={selectDate}
                        />
                    )}

                </List>
                <div
                    style={{
                        width: division9,
                        height: height
                    }}
                    className='list-but'
                    onClick={() => listBut(7)}
                >
                    <p></p>
                </div>
                // nextBut
            </div>
        )
    }
    }
</AutoSizer>


  
     20  .
     AutoSizer   
  div     /9 
 2 +  9   .



4.     
onItemsRendered={handleTile}  handleTile .

const handleTile = ({
    visibleStartIndex, 
    visibleStopIndex,
    //       
    //   index    index
}) => {
    setCenter(Math.round((visibleStartIndex + visibleStopIndex) / 2))
    // 7             2  
    //        

    if (visibleStartIndex < 10) {
        //        10 
        if (!addLoading) return; //  
        const addDate = async () => {
            setAddLoading(false) //  
            const newData = await loadDateData(dateData[0], 50, 1)
            //          50  
            setDateData(pre => [...newData, ...pre])
            //   
            if (listRef.current) {
                listRef.current.scrollTo(listRef.current.state.scrollOffset + newData.length * 44.44444444);
            }
            //      ,    
            setAddLoading(true);
            //  
        }
        addDate();
    }
    if ((dateData.length - 10) < visibleStopIndex) {
        //              10  
        const lastLength = dateData.length - 1 //  
        if (!addLoading) return; //  
        const addDate = async () => {
            setAddLoading(false) //  
            const newData = await loadDateData(dateData[lastLength], 50, 2)
            //          50  
            setDateData(pre => [...pre, ...newData])
            //   
            setAddLoading(true);
            //  
        }
        addDate();
        // RW 
    }
}


 - Tile Component

const Tile = ({ index, style, data, onClick, chooseIndex, selectDate }) => {
    const indexDate = moment(data[index]);
    const d = indexDate.format('dd')
    const dd = indexDate.format('DD')
    const todayCheck = today.isSame(indexDate, 'day')
    const chooseCheck = chooseIndex === index
    const selectCheck = selectDate.origindate.includes(indexDate.format('YYYY-MM-DD'))
    return (
        <div
            style={{
                ...style,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'flex-start',
                alignItems: 'center',
                userSelect: 'none',
                cursor: 'pointer'
            }}
            onClick={() => onClick(index)}
        >
            <p style={{
                color: chooseCheck ? 'blue' : 'black',
                fontWeight: chooseCheck ? 'bold' : 'normal'
            }}>
                {todayCheck ? '' : d} <br />
                {dd}
            </p>
            <div
                style={{
                    display: selectCheck ? 'block' : 'none',
                    width: '5px',
                    height: '5px',
                    marginTop: '5px',
                    borderRadius: '5px',
                    background: 'blue'
                }}
            />
        </div>
    )
};

`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function11;
