function Code1() {

    const codeText =
        `
// BaseBall_Team Front
// Post.js 


function Kakao(props: KakaoProps) {
type stadiummapType = {
    name: string;
    lat: number;
    lon: number;
    id: number;
};
let stadiumMap: stadiummapType[] = [
    { name: '', lat: 37.29967438659638, lon: 127.00980136384605, id: 17577962 },
    { name: '', lat: 37.51205948260811, lon: 127.07190263259021, id: 20740490 },
    { name: "", lat: 37.436899633771745, lon: 126.69329912761006, id: 8053130 },
    { name: '', lat: 35.19397929487557, lon: 129.06167478845023, id: 8396881 },
    { name: '', lat: 37.49823485357189, lon: 126.86719636030573, id: 7890664 },
    { name: '', lat: 35.22274249291124, lon: 128.5821497207587, id: 26542435 },
    { name: '', lat: 35.1681393644369, lon: 126.88910941215897, id: 21755436 },
    { name: '', lat: 36.31706721430381, lon: 127.42908737853345, id: 8131581 },
    { name: '', lat: 35.84102725825763, lon: 128.6815209789601, id: 12864272 }
]


let stadium = stadiumMap.filter((a) => a.name === props.stadium)
let [loading, setLoading] = useState<boolean>(false)

useEffect(() => {
    setLoading(true)

}, [])
useEffect(() => {
    if (loading) {
        var container = document.getElementById('map');
        var options = {
            center: new kakao.maps.LatLng(stadium[0]?.lat, stadium[0]?.lon),
            level: 3
        };
        var map = new kakao.maps.Map(container, options);
    }
}, [loading, stadium])
return (
    <>
        <div id="map" style={{ width: '100%', aspectRatio: '1/0.8' }} onClick={() => {
            window.open(\`https://map.kakao.com/link/to/\${stadium[0].id}\`);
        }}></div >
    </>
)
// KaKao Api       .
};
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function8;

