function Code1() {

    const codeText =
        `
//  

<Route path='/:id' element={<FcSocketIoFisrtGet />}> 
 //    
    <Route path='/:id/main' element={<MainPage />} />
    <Route path='/:id/history' element={<HistoryPage />} />
</Route>


//       
function FcSocketIoFisrtGet(): JSX.Element {
    const navigate = useNavigate()
    const dispatch = useAppDispatch();
    const { id } = useParams()

    useEffect(() => {
        if (!id) return
        joinRoomSocket(dispatch, id, navigate)
        const leaveRoomFc = () => {
            leaveRoomSocket(dispatch, id,)
        }
        window.addEventListener('beforeunload', leaveRoomFc)
        return () => {
            leaveRoomFc();
            window.removeEventListener('beforeunload', leaveRoomFc)
        }
    //  Parmas   Socket 
    //  
    }, [])

    const navigateFc = (location: number) => {
        if (location === 1) navigate(\`\/\${id}/main\`)
        if (location === 2) navigate(\`\/\${id}/history\`)
    }

    return <>
        <Nav navigateFc={navigateFc} />
        <Outlet />
    </>
}
    

//       2   .
//    Main /     History
//   ,  Socket Room 
//      Component   .
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function4;

