function Code1() {

    const codeText =
        `
// Story.tsx  StorySentence Component
function StorySentence(
    props: {
        sentenceData: SentenceType,
        sentenceClickFc: SentenceClickFcType,
        sentenceIndex: number,
    }): JSX.Element {

    const sentenceData = props.sentenceData;
    const sentenceClick: SentenceClickFcType = props.sentenceClickFc

    return <StorySentenceStyled
        $expresstion={sentenceData.expression}
        onClick={() => sentenceClick(sentenceData)}>
        {sentenceData.content}
    </StorySentenceStyled>
    // Styled-Component   span
    // Props Data   $   :
    //      $  
}

// styled.ts   Styled-Component

export const StorySentenceStyled = styled.span < StorySentenceStyledType > \`
    color : \${props => colorExtractor(props.$expresstion)};
    font-size:\${props => FontSizeExtractor(props.$expresstion)};

    // $props    color font-size    
    //  style  
    // Styled-component Type   
\`


const colorExtractor: ColorExtractorFcType = (expresstion) => {
    let returnValue = [0, 0, 0]
    const idxColorArrayRGB = [[225, 223, 0], [70, 130, 180], [220, 20, 60], [50, 205, 50]]
    //    =>  happiness, sadness, best, positive

    const maxValue = 150; //  100%
    const minValue = 30; //  0%  - 30    
    const maxExpresstionNumber = Math.max(...expresstion) //       , 
    const maxExpresstionIndex = expresstion.indexOf(maxExpresstionNumber); //   ,    idx

    if (maxExpresstionNumber > minValue && maxExpresstionNumber < maxValue) {
        const percentageOfMaxValue //  30 ~  150   
            = ((maxExpresstionNumber - minValue) / (maxValue - minValue));
        const conversionStyleRGB = idxColorArrayRGB[maxExpresstionIndex].map((val) => val * percentageOfMaxValue)
        returnValue = conversionStyleRGB
    } else if (maxExpresstionNumber >= maxValue) {
        returnValue = idxColorArrayRGB[maxExpresstionIndex]
    }
    return \`rgb(\${returnValue[0]},\${returnValue[1]},\${returnValue[2]})\`
}

const FontSizeExtractor: FontSizeExtractorFcType = (expresstion) => {
    let returnValue = 1
    let overMaxValue = false
    const maxValue = 1000; //  100%
    const minValue = 100; //  0%  - 100   1rem 
    const totalExpression = expresstion.reduce((pre, cur) => pre + cur)

    if (totalExpression > minValue && totalExpression < maxValue) {
        const percentageOfMaxValue
            = parseFloat(((totalExpression - minValue) / (maxValue - minValue)).toFixed(2));
        returnValue = returnValue + percentageOfMaxValue
    } else if (totalExpression >= maxValue) {
        returnValue = 2
        overMaxValue = true
    }

    return \`\${returnValue}rem\`
    // return \`\${returnValue}rem \${overMaxValue ? 'bold' : 'normal'}\`
}


// typeStyled.ts 


export interface StorySentenceStyledType {
    $expresstion: [number, number, number, number]
}
export type ColorExtractorFcType = (expresstion: [number, number, number, number]) => string
export type FontSizeExtractorFcType = (expresstion: [number, number, number, number]) => string
    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function3;
