function Code1() {

    const codeText =
        `
// Post.tsx  Bulletinpost Component
// Page Mount  useEffect      GET
//   State  

//  Element Code .

<div className='post-box'> 
    <div className={userLike ? 'like-button userlike' : 'like-button'}
    //    className  style .
        onClick={async () => {
            if (!userId) return alert('    .')
            setPostdataLike((pre) => userLike ? pre - 1 : pre + 1) //    (   )
            bulletin_like() // HTTP    
        }} >
        //   
        <p>{userLike ? "" : ""}</p>
        //   Text 
    </div>
</div>


async function bulletin_like() {
    setUserLike(!userLike) 
    //    (       )
    await fetch('/api/post/bulletin/like/' + id, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        body: JSON.stringify({
            userLike: !userLike,
        })
    }).catch((err) => {
        console.error(\` \${ err.status } = \` + err);
    })
}
    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}


function Code2() {

    const codeText =
        `
// Main.tsx   FcSocketIoFisrtGet Component  
// Params  Socket.io   
//  Params      
//   /     Room Join(Socket.io )  
//   
//     redux Toolkit Store 

//   2       

// ClickSentence.tsx  ClickSentencePage Component


    const mutation = useMutation(
        (data: { sentenceData: SentenceStoreSliceType, idx: number, params: string }) => expressionPost(data), {
        onSuccess: (data) => { },
        // HTTP         .
        onError: (error, data) => {
            dispatch(removeExpression([data.idx, data.sentenceData.no]))
            dispatch(removeExpressionT([data.idx, data.sentenceData.no]))
            alert('   .     .')
        }
        //  -   ,       
    })
    const expressionClickFc: ExpressionClickFc = (sentenceData, idx) => {
        if (!id) return; // .
        mutation.mutate({ sentenceData: sentenceData, idx: idx, params: id }) 
        // react-Query  Mutation   HTTP  
        dispatch(addExpression([idx, sentenceData.no]))
        dispatch(addExpressionT([idx, sentenceData.no]))
        //  -    Redux Store   
    }


     // JSX 
        <div className='ClickSentencePageWrap flex column jc-start ai-start'>
            <div className='clickSentenceContent flex row jc-start ai-center'>
                 //
            </div>
            <div className='clickSentenceExpression flex row ai-center'>
                {['', '', '', ''].map((val, idx) => <div key={idx}
                    onClick={() => expressionClickFc(clickSentenceStoreSlice, idx)}
                >
                    {val}   {clickSentenceStoreSlice.expression[idx]}
                </div>
                //     expressionClickFc  
                //       
                //     IDX  
                )}
            </div>
            <div className='clickSentenceInfo flex row ai-center'>
                //
            </div>
        </div>

    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}



export default Function2;
