function Code1() {

    const codeText =
        `
// BaseBall_Diry Front
// Login.js 


//    .
//  3    .
//     .


//1.   
// form input   text    state  .

<input type='text'
placeholder='- 5 ~ 15 (/)'
className='input'
name='username'
onChange={(e) => { formDataChange(e) }}
style={{ background: warningCss[0] }}
disabled={overlapCheck[0]}
autoComplete='off'
/>


//2. useEffect    
// onChange state  () useEffect   
//     0.3     
//   ,   clearTimeout   .

useEffect(() => {
    let inputTime;
    if (inputTime) {
        clearTimeout(inputTime)
    }
    inputTime = setTimeout(() => {
        formDataExamine(0) 
        //    
    }, 300);
    return () => {
        clearTimeout(inputTime)
    }
}, [myUserName])

// 3.       
//   state   state  style  
//    .

`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function9;

